export const cancelPreviousRequest = (prevReq) => {
    const response = {
        success: false,
        error: 'ignored',
        result: null,
        message: 'User ignored this transaction',
        data: prevReq,
        request_id: prevReq.request_id,
    };
    sendResponse(response);
};
export const sendRequestToBackground = (req, chrome) => {
    chrome.runtime.sendMessage({
        command: 'sendRequest',
        request: req,
        domain: window.location.hostname,
        request_id: req.request_id,
    });
};
export const sendIncompleteDataResponse = (value, error) => {
    let message = typeof error === 'string' ? error : error.stack;
    var response = {
        success: false,
        error: 'incomplete',
        result: null,
        message,
        data: value,
        request_id: value.request_id,
    };
    sendResponse(response);
};
/* istanbul ignore next */
export const sendResponse = (response) => {
    if (response.data.redirect_uri) {
        window.location.href = response.data.redirect_uri;
    }
    else {
        window.postMessage({
            type: 'steem_keychain_response',
            response,
        }, window.location.origin);
    }
};
